/* Header, Head */
#title-app{
  margin-top: 20px;
  font-size: 20px;
  color: #333;
  text-align: center;
  padding: 20px;
}

#divider-header {
  background-color: rgb(223, 223, 223);
  width: 90%;
  position: absolute;
  left: 5%;
}

/* Grid Article */
.grid-container-article{
  margin-top: 50px;
  margin-bottom: 40px;
}

#parrafo-grid-article-p{
  color: rgb(189, 189, 189);
}

.resalto-letras{
  color: #333;
}

.grid-parrafo-article{
  text-align: center;
}

/* Rutas */
.header-menu{
  text-align: center;
}

.ul-Grid{
  margin: 0;
  padding: 0;
  margin-top: 10px;
  list-style: none;
}

.li-Grid{
  display: inline-block;
  text-align: center;
}

.li-Grid{
  margin-right: 20px;
  margin-left: 20px;
  /* background-color: blue; */
}

.ul-Grid li a {
  text-decoration: none;
  color: #333;
  font-size: 12px;
}

/* Secuancia de números */
.num-inicial{
  text-align: right;
}

/* Footer */
footer{
  position: absolute;
  bottom: 0%;
  width: 100%;
}
.footer-ul{
  text-align: right;
  padding: 0;
  list-style: none;
}

.footer-li{
  display: inline-block;
  padding-left:20px;
  margin-right: 5px;
  font-size: 10px;
  color: #333;
  position: relative;
  top: 16px;
}

.footer-li:hover {
  text-decoration: underline;
}

#itemTag{
  position: relative;
  top: -5px;
  margin-right: 0px;
  margin-left: 10px;
}

/* IMG */
.grid-img{
  text-align: center;
}

.grid-img > img {
  width: 500px;
  margin-top: 50px;
}

/* Botón */

#submitBottom{
  text-align: center;
  position: relative;
  top: -10px;
}

#textBotton{
  font-weight: 100;
}

/* Botón Downlaod */
.bottonDownload{
  text-align: center;
  padding: 52px;
}


